        'Mit dem von der Stadt Wien geförderten Projekt <strong><em>Counter-Bot </em></strong>setzen sich ZARA – Zivilcourage und Anti-Rassismus-Arbeit gemeinsam mit seinen Projektpartner*innen mit Fragestellungen rund um "Künstliche Intelligenz im Einsatz gegen rassistische Hasspostings" auseinander. Ziel des Projekts ist die Erstellung einer wissenschaftlich fundierten Grundlage zur Implementierung eines Counter-Bots, der automatisch effektive Widerrede gegen rassistische Hasspostings erzeugt. Die im Rahmen des Projekts induktiv identifizierten Hinweise auf charakteristische Strukturen von Hasspostings werden linguistisch und statistisch analysiert, um eine brauchbare Ausgangsbasis für die zukünftige Implementierung des möglichen Counter-Bots zu liefern.<br /><br />Zu diesem Zwecke wird aus der großen Menge der von der ZARA-Beratungsstelle #GegenHassimNetz<em> </em>bereits auf Twitter gesammelten rassistischen Postings unter Anleitung von ZARA eine Stichprobe geeigneter Postings zur wissenschaftlichen Analyse gezogen. In einem ersten Analyseschritt wird ein Kategorienschema entwickelt, das die relevanten Merkmale der Postings einer statistischen Analyse zugänglich macht. In einem zweiten Schritt sollen charakteristische Muster in den Postingabfolgen anhand der ermittelten Kategorien wissenschaftlich identifiziert werden. Diese beiden Teilschritte ermöglichen ein analytisches Verständnis der Struktur von Hasspostings und der Wirkung von Gegenrede. Derartigen Mustern wird eine steuernde Rolle im Funktionsprinzip des Counter-Bots zukommen und wird integraler Bestandteil des Pflichtenheftes, das die Basis für die technische Realisierung des Counter-Bots in einem Folgeprojekt darstellt.<br /><br /><strong>Projektträger*innen: </strong>Ludwig Boltzmann Institut für Menschenrechte, Abt. Asyl, Anti-Diskriminierung und Diversität<br /><strong>Projektpartner*innen:</strong> Institut für Sprachwissenschaft der Universität Wien, Institut für Psychologie der Universität Klagenfurt, TUNNEL23, Zivilcourage und Anti-Rassismus-Arbeit<br /><strong>Projektlaufzeit</strong>: 01/2020 - 12/2020 <br /><strong>Gefördert von:</strong> MA7 - Kulturabteilung der Stadt Wien',
