      text:
        'Aktuell arbeitet das ZARA-Team gemeinsam mit TUNNEL23 an der Web-App »Schneller Konter«. Sie ermöglicht User*innen schnelle, einfache und kreative Reaktionen auf Hasspostings in Sozialen Medien und beugt somit "Silencing" vor. User*innen können direkt in der App auf diverse von Expert*innen entwickelte Kommunikations-Strategien und -Bausteine zugreifen, sich daraus selbst kreative Gegenrede zusammenstellen und diese sofort auf Social Media Plattformen posten.<br /><br />Das Tool unterstützt Menschen, die Soziale Medien nutzen, und die direkt Betroffene oder Zeug*innen von Hass im Netz sind. Es richtet sich also an jene, die das Phänomen Hass im Netz bereits wahrgenommen haben und etwas dagegen tun möchten - aber sich nicht sicher sind, ob sie etwas tun können bzw. wie sie vorgehen können. Viele User*innen haben die Erfahrung gemacht, dass bei hitzigen, hasserfüllten Debatten ausgewogene und differenzierte Gegenrede in Sozialen Medien untergeht oder gar mit weiterem Hass beantwortet wird. Klassische Versuche der Gegenrede wirken oft langsam, manchmal eventuell sogar unbeholfen bzw. uncool - alles Attribute, die im Netz zu wenig Sichtbarkeit und Effekt führen. Immer mehr Nutzer*innen ziehen sich daher ganz zurück oder mischen sich nicht mehr ein. Das schränkt die Meinungsvielfalt ein.<br /><br />Userinnen, die Hass im Netz (mit-)erleben, können mit dem neuen Tool sofort, einfach und vielleicht sogar lustvoll selber Gegenrede produzieren. Die einzelnen Bausteine sind von Expertinnen ausgewählt. User*innen können angekommen auf der Startseite der App zunächst einmal durchatmen. Hass im Netz kann enorm belastend sein, und es kann nützlich sein, sich zuerst zu sammeln, um dann gestärkt und gezielt mit Gegenrede gegen Hater*innen vorzugehen. Anhand bewährter Zielsetzungen und wirkungsvoller Strategien können User*innen dann ihre Interventionen individuell gestalten. Die App soll stärken und motivieren. Das führt zu weniger “Silencing” und zu mehr Zivilcourage im Internet.<br /><br />Information zum Fortschritt der WebApp finden Sie <a href="https://www.netidee.at/schneller-konter/schneller-konter-kennt-keinen-winterschlaf?fbclid=IwAR1TARhsUvfDzP4tu3t-6SvdQJ1q2kT2c5-QIjQ72qJyBlNUYuxD3vbQ1Uw">hier</a>.<br /><br /><strong>Projektträger*innen: </strong>ZARA - Zivilcourage und Anti-Rassismus-Arbeit<br /><strong>Projektpartner*innen:</strong> <a href="https://www.tunnel23.com/">TUNNEL23</a><br /><strong>Projektlaufzeit:</strong> 01/2020 - 07/2020 <br /><strong>Launch des »Schnellen Konter«:</strong> Ende September 2020 <br /><strong>Gefördert von: </strong><a href="https://www.netidee.at/netidee">netidee | IPA</a>',
