      text:
        'Mit dem EU-Projekt sCAN – Specialised Cyber Activists Network (2018-2020) setzte sich ZARA – Zivilcourage und Anti-Rassismus-Arbeit gemeinsam mit europäischen Partner*innenorganisationen auf unterschiedlichen Ebenen gegen Hass und Hetze im Internet ein. Das Projekt zielte darauf ab, online hate speech durch automatisierte Tools besser erkennen zu können, sowie Wissen über Cyber Hate und (trans-)nationale Entwicklungen zu sammeln und somit Gegenmaßnahmen auszubauen.<br /><br />Das Projekt stützte sich auf erfolgreich abgeschlossene europäische Projekte, wie zum Beispiel Research – Report – Remove: Countering Cyber Hate Phenomena und Facing Facts!, und strebte danach, all diese zivilgesellschaftlichen Initiativen fortzuführen, hervorzuheben und somit Online Hate Speech entgegenzuwirken. Durch die europaweite Kooperation konnten die Projektpartner*innen ihre Zusammenarbeit (auch) im Rahmen des <a href="http://www.inach.net/">Internationalen Netzwerks gegen Cyber Hate</a> (INACH) vertiefen und (weiter) intensivieren. Die #sCAN-Projektpartner*innen trugen dazu bei, relevante automatisierte Monitoring-Tools zu testen, auszuwählen und bereitzustellen, um die großflächige Erkennung hasserfüllter Inhalte zu verbessern. Ein weiterer wichtiger Aspekt von sCAN bestand darin, die Monitroing-Tätigkeiten des Konsortiums generell zu verstärken. Darüber hinaus zielte dieses Projekt auf die Entwicklung des europaweiten Capacity-Building ab, indem E-Learning-Kurse für Cyberaktivist*innen, Moderator*innen und Tutor*innen über die Facing Facts! Online Plattform angeboten wurden.<br /><br />Mehr dazu finden Sie hier: <a href="http://scan-project.eu/project">scan-project.eu/project</a><br /><br />sCAN war ein vom European Commission Directorate – General for Justice and Consumers finanziertes Projekt im Rahmen des Rights, Equality and Citizenship (REC)-Programms der Europäischen Union. Das Projekt wurde von dem Bundesministerium für Arbeit, Soziales, Gesundheit und Konsumentenschutz co-finanziert.<br /><br /><strong>Projektträger*innen: </strong>LICRA - Ligue International contre le Racisme <br /><strong>Projektpartner*innen:</strong>  <a href="http://www.licra.org/">LICRA</a> (Frankreich), <a href="http://www.ceji.org/">CEJI – A Jewish Contribution to an Inclusive Europe</a> (Belgien), <a href="https://humanrightshouse.org/human-rights-houses/zagreb/">Human Rights House Zagreb</a> (Kroatien), <a href="http://www.romea.cz/en/">Romea</a> (Tschechien), <a href="https://www.respectzone.org/en/">Respect Zone</a> (Frankreich), <a href="https://www.jugendschutz.net/">jugendschutz.net</a> (Deutschland), <a href="http://cilvektiesibas.org.lv/en/">Latvian Centre for Human Rights</a> (Lettland), <a href="https://www.fdv.uni-lj.si">University of Ljubljana, Faculty of Social Sciences</a> (Slowenien) und <a href="http://cesie.org/en/">CESIE</a> (Italien), ZARA-Zivicourage und Anti-Rassismus-Arbeit <br /><strong>Projektlaufzeit:</strong> Mai 2018 - April 2020<br /><strong>Gefördert von: </strong>Europäische Kommission & Bundesministerium für Soziales, Gesundheit, Pflege und Konsumentenschutz<br /><br /><strong><em>Es wurde ein sCAN Follow-Up Antrag eingereicht. ZARA hofft sehr, diese Arbeit im Jahr 2021 fortführen zu können.</em></strong>',
