      text:
        'Von August 2017 bis Jänner 2020 verstärkte ZARA über das Projekt V-START (Victim Support Through Awareness-Raising and neTworking), gemeinsam mit europäischen Partner*innen (COSPE, efms - Europäisches Forum für Migrationsstudien und Kuca ljudskih prava Zagreb / Human Rights House Zagreb), seine Unterstützung für von hate crime Betroffene.<br /><br />Doch was ist hate crime?<br /><br />Die praxisorientierte hate crime Definition vom <a href="https://www.osce.org/de/%3EOSZE%3C/a%3E-B%C3%BCro%20f%C3%BCr%20demokratische%20Institutionen%20und%20Menschenrechte%20(%3Ca%20href=">ODIHR</a> lautet, “Hate crimes sind kriminelle Handlungen mit einem Vorurteilsmotiv gegen bestimmte Personengruppen”. Diese Definition wird im Rahmen des Projektes herangezogen sowie kritisch beleuchtet.<br /><br />Die Tätigkeiten im Rahmen von V-START umfassten die Erstellung von länderspezifischen Berichten, den Ausbau eines gestärkten Netzwerks, die Erstellung von Handbüchern und Ratgebern sowie die Durchführung von Informations- und Awareness-Raising-Kampagnen. V-START zielte darauf ab:<br /><br /><ul><li>das Bewusstsein von Betroffenen für verfügbare Unterstützungsleistungen und –angebote zu steigern,<li>den Zugang zum Rechtssystem zu erleichtern<li>und das Unterstützungssystem durch spezifische Schulungen für Mitarbeiter*innen, sowie durch den Ausbau von Vernetzung und Kooperation zu verbessern.</li></ul><br /><br />Gemeinsam mit wichtigen Partner*innen der österreichischen Zivilgesellschaft konnte ein <strong>Netzwerk um hate crime sichtbarer zu machen</strong> gegründet werden: <a href="http://www.hatecrimekontern.at/">www.hatecrimekontern.at</a> <br /><br />Insgesamt betrug die Projektlaufzeit 30 Monate (August 2017 bis Jänner 2020). Das Projekt wurde über das <a href="http://ec.europa.eu/justice/grants1/programmes-2014-2020/justice/index_en.htm">Justizprogramm der Europäischen Union</a> und vom <a href="https://www.sozialministerium.at/site/">Bundesministerium für Arbeit, Soziales, und Konsumentenschutz </a>co-finanziert. Mit V-START wurde die Umsetzung der <a href="https://eur-lex.europa.eu/LexUriServ/LexUriServ.do?uri=OJ:L:2012:315:0057:0073:DE:PDF">EU-Richtlinie 2012/29/EU</a> zur Einführung und Festlegung von Mindeststandards im Rechts-, Unterstützungs- und Schutzbereich für Opfer von <em>hate crimes</em> unterstützt.<br /><br /><strong>Projektträger*innen: </strong>COSPE <br /><strong>Projektpartner*innen:</strong>  COSPE, efms - Europäisches Forum für Migrationsstudien und Kuca ljudskih prava Zagreb / Human Rights House Zagreb, ZARA-Zivilcourage und Anti-Rassismus-<br /><strong>Projektlaufzeit:</strong> Mai 2018 - April 2020<br /><strong>Gefördert von: </strong>Europäische Kommission & Bundesministerium für Soziales, Gesundheit, Pflege und Konsumentenschutz<br /><br /><strong><em>Es wurde ein V-START Follow-Up Antrag eingereicht. ZARA freut sich sehr, diese Arbeit ab Herbst 2020 intensiviert weiterführen zu können.</em></strong>',
      // <br /><br />Mehr Information zu V-START gibt es <a href="http://www.vstart.eu/">hier</a>.
